import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class TabPaneItem extends React.Component {
	constructor(props,context) {
		super(props);
		context.forceRenderPaneItem;
	}

	render() {
		const props = this.props;
		const {
			id,
			barId,
			selected,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('tab-pane-item',{
			['selected']:('selected' in props && selected !== false )
		},className);

		return (
			<div
				{...others}
				className={klassName}
				role="paneitem"
				id={id}
				aria-labeledby={barId}
				style={{display: selected ? 'block' : 'none'}}
			>
				{(this.context.forceRenderPaneItem || selected) ? children : null}
			</div>
		);
	}
};

TabPaneItem.propTypes = {
	id: PropTypes.string,
	barId: PropTypes.string,
	selected: PropTypes.bool,
	className: PropTypes.string,
	children: PropTypes.oneOfType([
		PropTypes.array,
		PropTypes.object,
		PropTypes.string
	])
};

TabPaneItem.contextTypes = {
	forceRenderPaneItem: PropTypes.bool
};

TabPaneItem.defaultProps = {
	selected: false,
	id: null,
	barId: null
};

export default TabPaneItem;